/**
 * Creole translation for bootstrap-datetimepicker>
 * (Not provided by standard npm install of bootstrap-datepicker)
 */
(function($){
    $.fn.datepicker.dates['ht'] = {
        days: ["dimanch", "lendi", "madi", "mkredi", "jedi", "vandredi", "samdi"],
        daysShort: ["dim", "len", "mad", "mek", "jed", "van", "sam" ],
        daysMin: ["d", "l", "ma", "me", "j", "v", "s", "d"],
        months: ["janvye", "fevriye", "mas", "avril", "me", "jen", "jiy", "out", "septanm", "oktb", "novanm", "desanm"],
        monthsShort: ["jan", "fev", "mas", "avr", "me", "jen", "jiy", "out", "sep", "okt", "nov", "des"],
        today: "Jodi a",
        suffix: [],
        meridiem: ["am", "pm"],
        weekStart: 1,
        format: "dd/mm/yyyy"
    };
}(jQuery));



